import 'bulma/css/bulma.css';
import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { Person } from '../types/Person';
import { getPeople } from '../api';
import { PeopleTable } from './PeopleTable/PeopleTable';

export const PeoplePage: React.FC = () => {
  const [people, setPeople] = useState<Person[]>([]);
  const location = useLocation();
  const navigate = useNavigate();
  const searchParams = new URLSearchParams(location.search);

  useEffect(() => {
    getPeople().then(peopleFromServer => {
      const peopleForTable = [...peopleFromServer]
        .map(personfromServer => {
          return ({
            ...personfromServer,
            mother: peopleFromServer
              .find(person => personfromServer.motherName === person.name)
              || null,
            father: peopleFromServer
              .find(person => personfromServer.fatherName === person.name)
              || null,
          });
        });

      setPeople(peopleForTable);
    });
  }, []);

  const query = searchParams.get('query') || '';
  const handleQueryChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    event.preventDefault();

    if (event.target.value) {
      searchParams.set('query', event.target.value);
    } else {
      searchParams.delete('query');
    }

    navigate(`?${searchParams.toString()}`);
  };

  let visiblePeople = [...people];

  if (query) {
    visiblePeople = visiblePeople
      .filter(person => {
        const lowerQuery = query.toLowerCase();

        if (person.motherName && person.motherName.toLowerCase()
          .includes(lowerQuery)) {
          return true;
        }

        if (person.fatherName && person.fatherName.toLowerCase()
          .includes(lowerQuery)) {
          return true;
        }

        if (person.name.toLowerCase().includes(lowerQuery)) {
          return true;
        }

        return false;
      });
  }

  return (
    <>
      <h1 className="title">People Page</h1>
      <div className="controll">
        <input
          type="text"
          name="filterInput"
          data-cy="filterInput"
          value={query}
          onChange={handleQueryChange}
        />
      </div>
      <PeopleTable people={visiblePeople} />
    </>
  );
};
